.blogsContainer {
        width: 60%;
        margin: auto;
        display: flex;
        flex-direction: column;
        padding: 30px;
      }
      .blogslist {
        margin-top: 50px;
        display: flex;
        flex-direction: column;
      }
      .blog {
        display: flex;
        flex-direction: column;
        padding: 20px;
        -webkit-box-shadow: 0px 13px 15px -8px rgba(0, 0, 0, 0.43);
        box-shadow: 0px 13px 15px -8px rgba(0, 0, 0, 0.06);
      }
      .blogTitle {
        font-size: 2em;
        font-weight: 600;
        display: flex;
        margin-bottom: 20px;
      }
      .blogType {
        width: 25%;
        background: #6246ea;
        color: white;
        display: flex;
        align-items: center;
        justify-content: center;
        line-height: 20px;
        border-radius: 10px;
      }
      .blogDescription {
        text-align: justify;
        font-weight: 400;
        line-height: 1.5em;
      }
      .blogDelete {
        width: 100px;
        padding: 10px 5px;
        background: #fc4569;
        color: white;
        border: none;
        border-radius: 5px;
        cursor: pointer;
      }
