.container {
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .wrapper {
        width: 30%;
        padding: 30px;
        -webkit-box-shadow: 0px 5px 15px 5px rgba(0, 0, 0, 0.12);
        box-shadow: 0px 5px 15px 5px rgba(0, 0, 0, 0.03);
        margin-top: 50px;

      }
      .header {
        font-size: 2em;
        font-weight: 600;
      }
      .createForm {
        margin-top: 20px;
        display: flex;
        flex-direction: column;
      }
      .createInput {
        flex: 1;
        min-width: 40%;
        margin: 10px 0;
        padding: 10px;
      }
      .createTextarea {
        padding: 10px 0;
        margin: 10px 0;
      }
      .createButton {
        width: 40%;
        border: none;
        padding: 15px 20px;
        cursor: pointer;
        background-color: #6246ea;
        color: white;
        margin-top: 10px;
        font-weight: 600;
        cursor: pointer;
      }
