.container {
        -webkit-box-shadow: 0px 5px 15px 5px rgba(0, 0, 0, 0.12);
        box-shadow: 0px 5px 15px 5px rgba(0, 0, 0, 0.03);
      }
      .wrapper {
        width: 80%;
        margin: auto;
        display: flex;
        align-items: center;
      }
      .header {
        flex: 3;
        margin-left: 10px;
      }
      .header a {
        text-decoration: none;
        color: #2b2c34;
      }
      .navbar {
        flex: 1;
        background-color: #fffffe;
        padding: 0 20px;
        display: flex;
        align-items: center;
        justify-content: space-around;
      }
      .navbar a {
        text-decoration: none;
        padding: 5px 10px;
        color: #2b2c34;
        line-height: 20px;
      }
      .navbar a[class='active'] {
        color: #fffffe;
        background: #6246ea;
        border-radius: 5px;
      }
